* {
  margin: 0;
  padding: 0;
}

.header__logo {
  width: 100%;
  height: 111px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #ffffff;
}

.header__logo--image {
  width: 201px;
  height: 52px;
}

.main__header {
  width: 250px;
  margin: 94px auto 0 auto;
}

.main__intro .main__title {
  text-align: center;
  font-size: 19px;
  color: #e17334;
  font-family: 'Merriweather', serif;
  font-weight: 900;
  margin: 35.4px auto 4px auto;
}

.main__intro .main__paragraph {
  font-family: 'OpenSans-Regular',serif;
  font-size: 15px;
  font-weight: 500;
  margin: 0 auto;
  text-align: center;
  color: #114e4e;
  width: 265px;
}

.main__intro .main__list {
  display: flex;
  justify-content: space-around;
  align-items: center;
  border: 1px solid #e17334;
  width: 268px;
  height: 74px;
  margin: 10px auto;
  border-radius: 4px;
}

.main__intro .main__list-item {
  text-decoration: none;
  list-style: none;
  padding: 10px;
  text-align: center;
}

.main__intro .main__list .main__list--text {
  color: #54585a;
  font-size: 16px;
  padding-top: 10px;
}

.main__intro .main__list .icon {
  color: #e17334;
  font-size: 28px;
}

.main__intro .main__button {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 30px 0;
}

.main__intro .main__button--start {
  font-size: 16px;
  font-family: 'OpenSans', serif;
  width: 129px;
  height: 42px;
  background-color: #438792;
  border: none;
  border-radius: 21px;
  color: #ffffff;
  text-transform: uppercase;
}

.footer {
  display: flex;
  flex-direction: column;
  text-align: center;
  height: 110px;
}

.footer__copyright {
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  color: #d8d8d8;
  margin-top: 20px;
}

.footer__logo {
  margin-top: 10px;
  margin-bottom: 20px;
}

.footer__logo--image {
  width: 103px;
  height: 40px;
}

.dropdown__desing {
  padding: 20px 30px 10px 31px;
  background-color: yellow;
  display: flex;
  align-items: center;
}

.dropdown__desing--icon {
  padding-right: 10px;
  color: #e17334;
}

.dropdown__desing--text {
  color: #54585a;
  font-size: 18px;
  font-family: 'OpenSans-Regular', serif;
  font-weight: 500;
  text-transform: uppercase;
}

.dropdown__desing--angleUp {
  text-align: right;
  padding-right: 20px;
}

@media all and (min-width: 768px) {
  .header__logo {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 40px;
  }

  .footer {
    flex-direction: row;
    justify-content: space-between;
    height: 60px;
  }

  .footer__copyright {
    margin-left: 40px;
  }

  .footer__logo {
    margin-right: 40px;
  }
}
